* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

a {
  font-size: 25px;
  text-decoration: none;
}

input {
  border: 2px solid #000;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #197bdd;
}

#articlesWrapper {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.article-card {
  width: 30%;
  text-align: center;
  background-color: #c1c1c1;
  border-radius: 15px;
  padding: 20px;
}

.article-img {
  width: 100%;
  border-radius: 15px;
}

.article-link {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(45, 45, 233, .73);
  border-radius: 15px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

